import React from 'react';
import {Link} from 'react-router-dom';
import "./Navigation.css";

function Navigation() {
    return (
        <div className="navi">
            <Link to="/">Home</Link>
            <Link to={{
                pathname: "/about",
                state   : {
                    fromNavigation: true
                }
            }}>About</Link>
            <Link to="./movie_detail">Detail</Link>
        </div>

    )
}

export default Navigation;